 * Replace them with your own styles or remove the file completely.
 */

.custom-bootstrap-container {
	border: 0.3px dashed #0074aa5e;
	padding: 5px;
	border-radius: 0px;
	background-color: #f0f8ff;
	transition: border-color 0.3s ease;
	min-height: 150px;
	box-sizing: border-box;
}

.custom-bootstrap-container:hover {
	border-color: #005177;
}

.custom-bootstrap-container {
	display: flex;
	flex-direction: column;
	width: 100%;
	min-height: 100%;
	box-sizing: border-box;
}

.custom-bootstrap-inner {
	flex: 1 1 auto;
	width: 100%;
	min-height: 100%;
	box-sizing: border-box;
	background-color: #ffffff;
	padding: 10px;
}
.block-list-appender.wp-block {
	position: relative !important;
	margin-top: 10px !important;
	margin-left: auto !important; /* ✅ এটিই ডান পাশে নেবে */
	max-width: 40px;
	max-height: 40px;
}
